import React,{Component} from 'react'
import './css/nav.css'
import logo from './asset/logo.png'
import {Link,NavLink} from 'react-router-dom';
import { FaCoffee,FaIoIosMenu} from 'react-icons/fa';
import {IoIosMenu} from 'react-icons/io';
class Nav extends Component{
	constructor(){
		super()
		this.state={
     togglemenuvalue:false,
     exwidth:50,
     forres:""
		}
    this.togglemenu=togglemenu.bind(this);
     function togglemenu(){
      if (this.state.togglemenuvalue) 
      {
         this.setState({togglemenuvalue:false});
        this.setState({forres:""});
      }
      else{
        this.setState({togglemenuvalue:true});
     this.setState({forres:"animatewidth"});
      }
     

     
     }
	}

 render(){
 	return(

       <div>
       

        <section>
        <div  className={this.state.togglemenuvalue?"forresponsivemenu animatewidth":"forresponsivemenu"}>
        <div className="resforlog">
        
        </div>
        <div className="resformenus">

        <ul>

       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/" className="reslink" activeClassName="linkactive">HOME</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/about" className="reslink"  activeClassName="linkactive">ABOUT</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/history" className="reslink" activeClassName="linkactive">HISTORY</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/gallery" className="reslink" activeClassName="linkactive">GALLERY</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/quotes" className="reslink" activeClassName="linkactive">QUOTES</NavLink></li>
        </ul>
        </div>
       </div>
       <div className={this.state.togglemenuvalue?"forbackresponsive fullwidth":"forbackresponsive"} onClick={()=>this.setState({togglemenuvalue:false})}>
       <p></p>
       </div>
       </section>
        :""
           
     
    
        <div className="fornav">
         
        <div className="forlog">

        <img src={logo} className="forlog" style={{height:"49px"}}/>
        </div>

        <div className="formenus">
        <ul>
       <li><NavLink to="/" className="link" activeClassName="linkactive">HOME</NavLink></li>
       <li><NavLink to="/about" className="link"  activeClassName="linkactive">ABOUT</NavLink></li>
       <li><NavLink to="/history" className="link" activeClassName="linkactive">HISTORY</NavLink></li>
       <li><NavLink to="/gallery" className="link" activeClassName="linkactive">GALLERY</NavLink></li>
       <li><NavLink to="/quotes" className="link" activeClassName="linkactive">QUOTES</NavLink></li>
    
        </ul>
        </div>
         <button type="button" className="buttontoggle" onClick={()=>this.togglemenu()}>
             <span>MENU</span><IoIosMenu size={30}/> 
         </button>
        </div>
        </div>

 		);
 }	
}
export default Nav