import React,{Component} from 'react';
import './css/header.css'
import {Container,Row,Col} from 'react-bootstrap';
import {Link} from  'react-router-dom'
import edit3 from './asset/edit3.png'
import {FaArrowAltCircleRight} from 'react-icons/fa'
class Header extends Component{
	constructor(){
		super();
	}
 render(){
  return(

  	<Container fluid>
     <Row className="forheader">
     {/*col==div start of headertextholder*/}
    <Col lg="8" sm="6" xs="12" className="forheaderText">
   
   <section className="fortexts">
   <h2>he is my hero.</h2>
   <h4>he is my inspiration.</h4>
   <h6>he is Rwandans gift from god.</h6>
   {FaArrowAltCircleRight}
   <br/>
   <Link to="/about" >
   <button type="button">Find More
  <FaArrowAltCircleRight size={30}/>
   </button>
   </Link>
   </section>

    </Col>

     {/*col==div start of headerimage*/}

    <Col lg="4" sm="6" xs="12" className="forheaderImage">
 
   <img src={edit3} alt="image of president paul kagame"/> 


    </Col>

    </Row>

  	</Container>
  )
}
}
export default Header
